body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html,body {
  width: 100%;
  height: 100%;
}
.root{
    width: 100%;
    height: 100%;
}
.container {
  display: flex;
  width: 100%;
  height: 100%;
  text-align: center;

}
.container__image {
  width: 40%;
  object-fit: cover;
}

.container__content {
  width: 60%;
  padding: 100px;
  display: flex;
  flex-direction: column;
}

.container__content_icons{
  list-style: none;
  display: flex;
  justify-content: center;
  align-content: center;
  width: 100%;
  padding: 0;
}
.container__content_icons__github__link{
  margin: 10px 10px 10px 10px;
}
.container__content_icons__linkedin__link{
  margin: 10px 10px 10px 10px;
}
.container__content_icons__twitter__link{
  margin: 10px 10px 10px 10px;
}
